import React from 'react'
import addToMailchimp from 'gatsby-plugin-mailchimp'

export default class Mailchimp extends React.Component {
  constructor(props) {
    super(props)
    this.state = {value: ''}
    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }
  
  handleChange(event) {
    this.setState({value: event.target.value})
  }
 
  handleSubmit = async (e) => {
    e.preventDefault
    const result = await addToMailchimp(this.state.value, {})
  }
 
  render () {
    return (
      <form onSubmit={this.handleSubmit} style={{display: 'block', marginTop: '-10px'}}>
        <input placeholder='yourname@email.com' type='email' value={this.state.value} onChange={this.handleChange} style={{padding: '7px 10px', minWidth: '260px', borderRadius: "5px", border: '1px solid rgba(0, 0, 0, 0.1)', margin:'10px 5px 0 0'}}/>
        <input type='submit' value='Get Notified' style={{color: '#fff', backgroundColor: 'rgb(90, 181, 164)', border: '1px solid rgba(255, 255, 255, 0.9)', padding: '7px 12px', borderRadius: '5px', marginTop: '10px', fontWeight: 200}}/>
      </form>
    )
  }
}


